nav{
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 10;
    position: fixed;
    left:50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 1.5rem;
    backdrop-filter: blur(15px);
    border-radius: 2rem;
}
nav a {
    background: transparent;
    padding: 0.5rem;
    border-radius: 50%;
    color: var(--color-light);
    font-size: 1.1rem;
}
nav a:hover{
    background: rgba(0, 0, 0, 0.2);
}
nav a.active{
    background: var(--color-primary);
    color: var(--color-bg);
}