@import url('https://fonts.googleapis.com/css2?family=Fredoka:wght@300&display=swap');

header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}
.header_container{
    text-align: center;
    height: 100%;
    position: relative;
}
.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}
.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left :0;
    bottom: 3rem;
}
.header__socials::after{
    content: '';
    width: 1px;
    height: 3rem;
    background: var(--color-primary);
}
.me{
    background: linear-gradient(var(--color-primary),transparent);
    width: 20rem;
    height: 45rem;
    position: absolute;
    left: calc(50% - 10rem);
    margin-top: 1rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 3rem 1.5rem 1.5rem 1.5rem;
}
.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 500;
    font-size: 1.2rem;
}
@media screen and (max-width:1024px){
   .header{
       height: 68vh;
   }
}
@media screen and (max-width:600px){
    .header{
        height: 100vh;
    }
    .header__socials,
    .scroll__down{
        display: none;
    }
}